html {
  width: 100%;
  height: 100%;
}
html body {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #066;
  color: #eee;
  font-family: 'Lato', sans-serif;
  font-weight: 100;
  -webkit-text-stroke: 0.5px;
  overflow: hidden;
  cursor: none;
}
html body * {
  cursor: none;
}
.mouse .pointer {
  position: absolute;
  left: -0.25rem;
  top: -0.25rem;
  width: 0.5rem;
  height: 0.5rem;
  background-color: #eee;
  border-radius: 100%;
  transform: translate(0, 0);
  will-change: transform;
}
.mouse .particles {
  position: absolute;
  left: -0.25rem;
  top: -0.25rem;
  width: 0.5rem;
  height: 0.5rem;
  transform: translate(0, 0);
  will-change: opacity, transform;
}
.mouse .particles .particle {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.mouse .particles .particle::after {
  content: '';
  position: absolute;
  left: 0.125rem;
  top: -2.375rem;
  width: 1rem;
  height: 0.25rem;
  background-color: #eee;
  border-radius: 100%;
  transform-origin: 50% 2.5rem;
  transform: rotateZ(0);
  animation: particle-movement linear infinite;
  animation-duration: 1s;
  will-change: transform;
}
.mouse .particles .particle.index-1 {
  transform: rotateZ(0) scaleX(0.25);
}
.mouse .particles .particle.index-1::after {
  animation-delay: 0s;
}
.mouse .particles .particle.index-2 {
  transform: rotateZ(60deg) scaleX(0.25);
}
.mouse .particles .particle.index-2::after {
  animation-delay: 0.333333333333333s;
}
.mouse .particles .particle.index-3 {
  transform: rotateZ(120deg) scaleX(0.25);
}
.mouse .particles .particle.index-3::after {
  animation-delay: 0.666666666666667s;
}
